import { Pipe, PipeTransform } from '@angular/core';
import { Arrangement, hasExternalInfo } from '@backbase/product-summary-common-ang';

/**
 * Verifies if account has external info
 */
@Pipe({
  name: 'bbHasExternalInfo',
})
export class HasExternalInfoPipe implements PipeTransform {
  transform(account: Arrangement | undefined): boolean {
    return account ? hasExternalInfo(account) : false;
  }
}
